import React from "react";

export default function SignupPage() {
  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-50">
      <form className="bg-white p-8 rounded shadow-md w-80">
        <h2 className="text-2xl font-bold mb-6 text-center">Sign Up</h2>
        <input type="email" placeholder="Email" className="w-full mb-4 p-2 border rounded" required />
        <input type="password" placeholder="Password" className="w-full mb-4 p-2 border rounded" required />
        <input type="password" placeholder="Confirm Password" className="w-full mb-6 p-2 border rounded" required />
        <button type="submit" className="w-full bg-green-600 text-white py-2 rounded hover:bg-green-700">Sign Up</button>
        <div className="mt-4 text-center text-sm">
          Already have an account? <a href="/login" className="text-green-600 hover:underline">Login</a>
        </div>
      </form>
    </div>
  );
}
